body{
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  text-align: center;
  font-family: 'Oswald', sans-serif;
}
h1{
  margin: 0;
  margin-bottom: 1.1rem;
  font-size: 2.5rem;
  /* margin-right: 3rem; */
  margin-left: 1rem;

}
.input{
  border: 2px solid #333;
  border-radius: .3em;
  padding: .25rem;
  width: 15em;
  height: 2em;
}
select{
  margin-left: .5em;
  height: 3em;
  border-radius: .3em;
  width: 5em;
}
.equals{
  font-weight: bold;
  font-size: 3.5rem;
  margin-right: 3rem;
}